var success = false
export function splid() {
  var $ = function(selector:any){
    return  document.querySelector(selector);
  }
  var box = $(".drag")
  var bg = $(".bg")
  var text = $(".text")
  var btn = $(".btn")
  var err = $(".errr")
  var distance = box.offsetWidth - btn.offsetWidth;
  btn.onmousedown = function(e:any){
    btn.style.transition = "";
    bg.style.transition ="";
    e = e || window.event;
    var downX = e.clientX;
    err.innerHTML=''
    document.onmousemove = function(e){
        e = e || window.event;
        var moveX = e.clientX;
        var offsetX = moveX - downX;
        if( offsetX > distance){
            offsetX = distance;
        }else if( offsetX < 0){
            offsetX = 0;
        }
        btn.style.left = offsetX + "px";
        bg.style.width = offsetX + "px";
        if( offsetX === distance){
            text.innerHTML = "验证通过";
            // text.style.color = "#fff";
            btn.style.color = "green";
            btn.style.background = "url(data:image/png;base64,) no-repeat center center";
            btn.style.backgroundSize = "100% 100%";
            bg.style.backgroundColor = "lightgreen";
            success = true;
            btn.onmousedown = null;
            document.onmousemove = null;
        }
    }
    document.onmouseup = function(e){
        if(success){
            return;
        }else{
            btn.style.left = 0;
            bg.style.width = 0;
            btn.style.transition = "left 1s ease";
            bg.style.transition = "width 1s ease";
        }
        document.onmousemove = null;
        document.onmouseup = null;
    }
  }
}
export function slideFinishState(){
    return success
}
export function setErr(msg:string){
  var $ = function(selector:any){
    return  document.querySelector(selector);
  }
  var err = $(".errr")
  err.innerHTML=msg
}
export function resetSuccess(){
  success = false
}